.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #40444d;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(20px + 2vmin);
  color: white;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #40444d;
  min-height: 10hv;
  max-height: 15hv;
}

footer {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #27324d;
  min-height: 30hv;
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
}

.topnav {
  overflow: hidden;
  /* background-color: #40444d; */
  background-image: linear-gradient(to right, red,orange,yellow,green,blue,indigo,violet);
}

.topnav a {
  float: left;
  color: black;

  text-align: center;

  text-decoration: none;
  font-size: 2vh;
  padding: 1em 0em;
}

.topnav a:hover {
  background-color: rgba(221, 221, 221, 0.158);
  color: black;
  transition: 1s;
}


.topnav div a span
{
  background-color: var(--c);
  /* transform: translateY(15%); */
  border-radius: 1%;
  left: 0;
  transition: 0.5s;
  z-index: -1;
}

.topnav:focus{
  outline: 0;
}

.Button {
  --c: goldenrod;
  color: var(--c);
  float: left;
  color: #1b7ad3;
  text-align: center;
  text-decoration: none;
  font-size: 17px;
  overflow: hidden;
  /* background-color: #2163b9; */

  color: white;
  outline: none;
  border: none;
  width: 20vw;
  transition: 0.5s;

}


.ButtonActive {
  float: left;
  color: #000000;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  background-color: rgba(255, 255, 255, 0.308);
  border: none;
  outline: 0;
  transition: 0.5s;
  width: 20vw


}

.Button span {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: var(--c);
  border-radius: 0%;
  left: calc((var(--n)));
  transition: 1.5s;
  transition-delay: 0;
  z-index: -1;
}

div li span:nth-child(1) {
  --n: 1;
}

div li span:nth-child(2) {
  --n: 2;
}

div li span:nth-child(3) {
  --n: 3;
}

div li span:nth-child(4) {
  --n: 4;
}

.box {
  width: 80vw;
  border-radius: 1vh;
  margin: 0px;
  background:#d3d3d354;
  padding: 50px;
  text-align: center;
  font-weight: 900;
  color: #fff;
  font-family: arial;
  position:relative;
}

.sb1:before {
  content: "";
  width: 0px;
  height: 10px;
  position: absolute;
  border-left: 10px solid #d3d3d354;
  border-right: 10px solid transparent;
  border-top: 10px solid #d3d3d354;
  border-bottom: 10px solid transparent;
  right: 40vw;
  top: 0px;
}

.sb1:after {
  content: "";
  width: 0px;
  height: 0px;
  position: absolute;
  border-left: 7px solid transparent;
  border-right: 7px solid #fff;
  border-top: 7px solid #fff;
  border-bottom: 7px solid transparent;
  left: -11px;
  top: 10px;
}

.trapezoid {
  background-image: linear-gradient(to right, red,orange);
  border-bottom: 5vh solid blue;
  border-left: 0vw solid red;
  border-right: 20vw solid red;
  height: 0;
  width: 20vw;
}

